	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"McDonald's Takes On Starbucks","date":"07 Jan 2008","slug":"/2008-01-07-mcdonalds-takes-on-starbucks/","tags":"Y2008"},"html":"<p>Today's Wall Street Journal reports on McDonald's plans to <a href=\"http://online.wsj.com/article/SB119967000012871311.html?mod=yahoo_hs&amp;ru=yahoo\">add coffee bars to its 14,000 US restaurants</a> by the end of this year (WSJ is not yet free, but the link should work for a few days). They will be similar but not quite the same as the McCafés in several hundred German McDonald's, including our restaurant in Dibbersen. It sounds like they will be putting the espresso machines behind the front counter instead of a separate shop-in-shop, in part to be able to serve all coffee products in drive-thru (German McCafé sells only in-store). It sounds like the <a href=\"http://www.beverageworld.com/content/view/34073/\">investment will be $100,000 per store</a> (total for 14,000 stores: $1.4 billion), but with an expected sales increase of $1 billion per year, it should pay off in a hurry.</p>\n<p>Despite the headline, I don't think Starbucks has to worry. Customers wanting the full corporate coffee lounge experience will go out of their way and pay the higher prices. I also think it's interesting that US McDonald's is taking an innovation developed outside the US. Over half of McDonald's are outside the US, and with the US market fairly saturated, the exciting markets are also overseas.</p>"},"next":{"fields":{"title":"Achieving z","date":"31 Dec 2007","slug":"/2007-12-31-achieving-z/"}},"previous":{"fields":{"title":"Coupons Coupons Coupons","date":"08 Jan 2008","slug":"/2008-01-08-coupons-coupons-coupons/"}}},{"node":{"fields":{"title":"Achieving z","date":"31 Dec 2007","slug":"/2007-12-31-achieving-z/","tags":"Y2007"},"html":"<p>One month after our re-opening we're very happy with the new look and the McCaf&eacute;. Our customers seem to be happy with it as well. I can't talk about numbers here, so I'll use letters instead. </p>\n<p>Our monthly sales started at a level of *x*, and in our business plan we estimated that after the remodel they would increase to *y*. After 3 to 5 years of hard work and good business practices we estimated we could achieve a level of *z*.</p>\n<p>As of yesterday we have already achieved *z* for December 2007.</p>\n<p>That of course does not rule out the hard work and good business practices for the future... we want *z* to be the average for all months, and not just be the best month of the year. But it's yet another sign we're moving in the right direction.</p>\n<p>In the short run, achieving *z* when your crew schedule and logistics have been planned for *y* presents problems of its own (i.e. short of crew and low on stock). </p>\n<p>And even when you've achieved *z* that doesn't mean you can rest on your laurels. There's always the next step, *z*+1.</p>\n<p>And on that note... Happy New Year, everyone!</p>"},"next":{"fields":{"title":"Filmmaker Christopher","date":"30 Dec 2007","slug":"/2007-12-30-filmmaker-christopher/"}},"previous":{"fields":{"title":"McDonald's Takes On Starbucks","date":"07 Jan 2008","slug":"/2008-01-07-mcdonalds-takes-on-starbucks/"}}},{"node":{"fields":{"title":"Filmmaker Christopher","date":"30 Dec 2007","slug":"/2007-12-30-filmmaker-christopher/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/2149334928/\" title=\"CIMG0417 by PapaScott, on Flickr\"><img src=\"http://farm3.static.flickr.com/2135/2149334928_d444855beb.jpg\" width=\"500\" height=\"375\" alt=\"CIMG0417\" /></a></p>\n<p>Christopher's trying his hand at stop-motion Playmobil animation. It would be cool except he's hogging my MacBook!</p>"},"next":{"fields":{"title":"christopher is 8","date":"27 Dec 2007","slug":"/2007-12-27-christopher-is-8/"}},"previous":{"fields":{"title":"Achieving z","date":"31 Dec 2007","slug":"/2007-12-31-achieving-z/"}}}],"pathPrefix":"","first":false,"last":false,"index":89,"pageCount":1101}}

